import React, { useState, useEffect } from 'react';
import Card from './components/ui/Card'; // AsegÃºrate de que la ruta es correcta
import mouseClickSound from './sounds/mouse-click.mp3';
import timeUpSound from './sounds/time-up.mp3';

const characters = [
  { id: 1, name: "Sarah", imagePath: "/images/who-is-who/1.sarah.png" },
  { id: 2, name: "John", imagePath: "/images/who-is-who/2.john.png" },
  { id: 3, name: "Maria", imagePath: "/images/who-is-who/3.maria.png" },
  { id: 4, name: "James", imagePath: "/images/who-is-who/4.james.png" },
  { id: 5, name: "Emma", imagePath: "/images/who-is-who/5.emma.png" },
  { id: 6, name: "David", imagePath: "/images/who-is-who/6.david.png" },
  { id: 7, name: "Lisa", imagePath: "/images/who-is-who/7.lisa.png" },
  { id: 8, name: "Michael", imagePath: "/images/who-is-who/8.michael.png" },
  { id: 9, name: "Anna", imagePath: "/images/who-is-who/9.anna.png" },
  { id: 10, name: "Thomas", imagePath: "/images/who-is-who/10.thomas.png" },
  { id: 11, name: "Sofia", imagePath: "/images/who-is-who/11.sofia.png" },
  { id: 12, name: "Emily", imagePath: "/images/who-is-who/12.emily.png" },
  { id: 13, name: "Robert", imagePath: "/images/who-is-who/13.robert.png" },
  { id: 14, name: "Lucy", imagePath: "/images/who-is-who/14.lucy.png" },
  { id: 15, name: "William", imagePath: "/images/who-is-who/15.william.png" }
];

const WhosWhoGame = () => {
  const [timeLeft, setTimeLeft] = useState(60);
  const [gameOver, setGameOver] = useState(false);

  useEffect(() => {
    if (timeLeft > 0) {
      const timer = setTimeout(() => setTimeLeft(timeLeft - 1), 1000);
      return () => clearTimeout(timer);
    } else {
      setGameOver(true);
      new Audio(timeUpSound).play();
    }
  }, [timeLeft]);

  const handleCardClick = () => {
    new Audio(mouseClickSound).play();
  };

  return (
    <div>
      <div className="timer">Time Left: {timeLeft}s</div>
      <div className="grid-container">
        {characters.map(person => (
          <Card key={person.id} person={person} onClick={handleCardClick} />
        ))}
      </div>
      {gameOver && <div className="game-over">Game Over</div>}
    </div>
  );
};

export default WhosWhoGame;
